import React from "react"

import IntroductionContent from "../../utils/IntroductionContent"
import Banner from "../../utils/Banner"
import { ISolutionCard } from "../../../interfaces"

import SolutionCard from "./SolutionCard"
import { translate } from "../../utils/translate"

interface IProps {
	solutions: Array<ISolutionCard>
}

export default function Solutions(props: IProps) {
	const solutions = props.solutions

	return (
		<div>
			<Banner titlePage={translate('banner.solution')} />
			<IntroductionContent
				title={translate('introduction.solution.title')}
				subtitle={translate('introduction.solution.subTitle')}
				content={translate('introduction.solution.content')}
			/>

			<div className="container">
				<div className="row">
					{solutions.map((item, idx) => (
						<div
							className={"col-md-12 col-lg-6 col-xl-" + item.colSize}
							key={idx}
						>
							<SolutionCard {...item} />
						</div>
					))}
				</div>
			</div>
		</div>
	)
}
