import styled from "styled-components"

export const Container = styled.div`
	font-family: "Montserrat", Sans-serif;
	padding: 40px;
	position: relative;
	min-height: 320px;
	background-image: url(${props => props.bgImage});
	background-position: center center;
	background-size: cover;
	border-radius: 5px;
	margin-bottom: 30px;
	color: #fff;
	cursor: pointer;

	.background-overlay {
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		position: absolute;
		border-radius: 5px;
		background-color: transparent;
		background-image: linear-gradient(70deg, #13414f 0%, #115964 100%);
		opacity: 0.75;
	}

	.content-wrapper {
		position: relative;
	}

	&:hover {
		color: black;
		transform: scale(1.05);

		.background-overlay {
			background-image: linear-gradient(70deg, #f09433 0%, #a58363 100%);
		}

		a {
			background-color: #13414f;
		}
	}
`

export const CardTitle = styled.h3`
	font-size: 25px;
	font-weight: 600;
	line-height: 1.5em;

	i {
		font-weight: 300;
		font-style: italic;
		font-size: 19px;
	}
`

export const CardDescription = styled.p`
	font-size: 16px;
	font-weight: 300;
	line-height: 1.6em;
	margin-right: 92px;
`

export const CardButton = styled.a`
	position: relative;
	border-radius: 50%;
	padding: 20px;
	line-height: 0;
	background-color: #f4bf8c;
	position: absolute;
	right: 32px;
	bottom: 32px;
`
