import React from "react"
import { FaChevronRight } from "react-icons/fa"

import { Container, CardButton, CardTitle, CardDescription } from "./styles"
import { ISolutionCard } from "../../../../interfaces"
import { translate } from "../../../utils/translate"

const SolutionCard = ({
	title,
	description,
	slug,
	thumbnailUrl,
}: ISolutionCard) => {
	const handleClick = () => {
		window.open(slug, "_self")
	}

	return (
		<Container bgImage={thumbnailUrl} onClick={handleClick}>
			<div className="background-overlay"></div>

			<div className="content-wrapper">
				<CardTitle>{translate(`solutions.${title}.title`)}</CardTitle>
				<CardDescription>{translate(`solutions.${title}.description`)}</CardDescription>
				<b>{translate('solutions.details')}</b>
			</div>

			<CardButton>
				<FaChevronRight color="ffffff" size="20" />
			</CardButton>
		</Container>
	)
}

export default SolutionCard
