import React from "react"

import { Container, SubContainer } from "./styles"

interface IBanner {
	titlePage: string
}

const Banner = (props: IBanner) => {
	return (
		<Container>
			<div className="container">
				<div className="row">
					<div className="col-sm">
						<SubContainer>
							<p>KIVALITA CONSULTING</p>
							<h1>{props.titlePage}</h1>
						</SubContainer>
					</div>
				</div>
			</div>
		</Container>
	)
}

export default Banner
