import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/Layout"
import SolutionsComponent from "../components/pages/Solutions"
import { ISolutionCard } from "../interfaces"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

interface IProps {
	data: {
		allSolutionsJson: {
			edges: Array<{ node: ISolutionCard }>
		}
	}
}

const Solutions = ({ data }: IProps) => {
	const solutions: Array<ISolutionCard> = data.allSolutionsJson.edges.map(
		dataNode => dataNode.node
	)

	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "solutions",
			name: translate("breadCrumb.solutions"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Soluções para o seu negócio"
				description="Buscando soluções em qualidade? A Kivalita Consulting pode te ajudar! Conheça os nossos serviços e cases de sucesso para atender o seu negócio."
			></SEO>

			<SolutionsComponent solutions={solutions} />
		</Layout>
	)
}

export default Solutions

export const SolutionsQuery = graphql`
	query SolutionsQuery {
		allSolutionsJson {
			edges {
				node {
					colSize
					slug
					title
					description
					thumbnailUrl
				}
			}
		}
	}
`
